export function initMain() {
  const awardsListContainer = document.querySelector('.portfolio-list');
  const awardPreview = document.querySelector('.portfolio-image-list');
  const awardsList = document.querySelector('.portfolio-list');

  const POSITIONS = {
    BOTTOM: '0em',
    MIDDLE: '-4.5em',
    TOP: '-9em',
  };

  const lastMousePosition = { x: 0, y: 0 };
  let activeAward = null;
  let ticking = false;
  let mouseTimeout = null;
  let isMouseMoving = false;

  const awardsElements = document.querySelectorAll('.portfolio-item');

  const animatePreview = () => {
    const awardsListRect = awardsList.getBoundingClientRect();
    if (
      lastMousePosition.x < awardsListRect.left ||
      lastMousePosition.x > awardsListRect.right ||
      lastMousePosition.y < awardsListRect.top ||
      lastMousePosition.y > awardsListRect.bottom
    ) {
      const previewImages = awardPreview.querySelectorAll('img');
      previewImages.forEach((img) => {
        gsap.to(img, {
          scale: 0,
          duration: 0.4,
          ease: 'power2.out',
          onComplete: () => img.remove(),
        });
      });
    }
  };

  const updateAwards = () => {
    animatePreview();

    if (activeAward) {
      const rect = activeAward.getBoundingClientRect();
      const isStillOver =
        lastMousePosition.x >= rect.left &&
        lastMousePosition.x <= rect.right &&
        lastMousePosition.y >= rect.top &&
        lastMousePosition.y <= rect.bottom;

      if (!isStillOver) {
        const wrapper = activeAward.querySelector('.portfolio-link');
        const leavingFromTop = lastMousePosition.y < rect.top + rect.height / 2;

        gsap.to(wrapper, {
          y: leavingFromTop ? POSITIONS.TOP : POSITIONS.BOTTOM,
          duration: 0.4,
          ease: 'power2.out',
        });
        activeAward = null;
      }
    }

    awardsElements.forEach((award, index) => {
      if (award === activeAward) return;

      const rect = award.getBoundingClientRect();
      const isMouseOver =
        lastMousePosition.x >= rect.left &&
        lastMousePosition.x <= rect.right &&
        lastMousePosition.y >= rect.top &&
        lastMousePosition.y <= rect.bottom;

      if (isMouseOver) {
        const wrapper = award.querySelector('.portfolio-link');
        const enterFromTop = lastMousePosition.y < rect.top + rect.height / 2;

        gsap.to(wrapper, {
          y: POSITIONS.MIDDLE,
          duration: 0.4,
          ease: 'power2.out',
        });
        activeAward = award;
      }
    });

    ticking = false;
  };

  document.addEventListener('mousemove', (e) => {
    lastMousePosition.x = e.clientX;
    lastMousePosition.y = e.clientY;

    isMouseMoving = true;
    if (mouseTimeout) {
      clearTimeout(mouseTimeout);
    }

    const awardsListRect = awardsList.getBoundingClientRect();
    const isInsideAwardsList =
      lastMousePosition.x >= awardsListRect.left &&
      lastMousePosition.x <= awardsListRect.right &&
      lastMousePosition.y >= awardsListRect.top &&
      lastMousePosition.y <= awardsListRect.bottom;

    if (isInsideAwardsList) {
      mouseTimeout = setTimeout(() => {
        isMouseMoving = false;
        const images = awardPreview.querySelectorAll('img');
        if (images.length > 1) {
          const lastImage = images[images.length - 1];
          images.forEach((img) => {
            if (img !== lastImage) {
              gsap.to(img, {
                scale: 0,
                duration: 0.4,
                ease: 'power2.out',
                onComplete: () => img.remove(),
              });
            }
          });
        }
      }, 2000);
    }

    animatePreview();
  });

  document.addEventListener(
    'scroll',
    () => {
      if (!ticking) {
        requestAnimationFrame(() => {
          updateAwards();
        });
        ticking = true;
      }
    },
    { passive: true }
  );

  const previewImagesCMS = document.querySelectorAll('.portfolio-image-wrap img');

  awardsElements.forEach((award, index) => {
    const wrapper = award.querySelector('.portfolio-link');
    let currentPosition = POSITIONS.TOP;

    award.addEventListener('mouseenter', (e) => {
      activeAward = award;
      const rect = award.getBoundingClientRect();
      const enterFromTop = e.clientY < rect.top + rect.height / 2;

      if (enterFromTop || currentPosition === POSITIONS.BOTTOM) {
        currentPosition = POSITIONS.MIDDLE;
        gsap.to(wrapper, {
          y: POSITIONS.MIDDLE,
          duration: 0.4,
          ease: 'power2.out',
        });
      }

      // CREATE IMG FROM CMS
      const img = document.createElement('img');
      img.style.position = 'absolute';
      img.style.top = 0;
      img.style.left = 0;
      img.style.scale = 0;
      img.style.zIndex = Date.now();

      // Ambil sumber gambar CMS berdasarkan index
      if (previewImagesCMS[index]) {
        img.src = previewImagesCMS[index].src;
      }

      awardPreview.appendChild(img);

      gsap.to(img, {
        scale: 1,
        duration: 0.4,
        ease: 'power2.out',
      });
    });

    award.addEventListener('mouseleave', (e) => {
      activeAward = null;
      const rect = award.getBoundingClientRect();
      const leavingFromTop = e.clientY < rect.top + rect.height / 2;

      currentPosition = leavingFromTop ? POSITIONS.TOP : POSITIONS.BOTTOM;
      gsap.to(wrapper, {
        y: currentPosition,
        duration: 0.4,
        ease: 'power2.out',
      });
    });
  });
}

export function initAos() {
  const aos = document.querySelectorAll('[data-aos]');
  if (aos.length) {
    aos.forEach((el) => {
      const offset = parseFloat(el.getAttribute('data-aos-offset') || '3');
      gsap.from(el, {
        y: `${offset}em`,
        duration: 1.25,
        ease: 'smooth',
        opacity: 0,
      });
    });
  }
}

export function initAosScale() {
  const aos = document.querySelectorAll('[data-aos-scale]');
  if (aos.length) {
    aos.forEach((el) => {
      gsap.from(el, {
        scale: 0,
        duration: 1.25,
        ease: 'primary-ease-out',
        opacity: 0,
      });
    });
  }
}

export function initStaggeredAos() {
  const aos = document.querySelectorAll('[data-staggered-aos]');
  if (!aos.length) return;

  aos.forEach((el) => {
    const children = el.querySelectorAll('[aos-item]');
    if (!children.length) return;

    // --- Direction (start | end | random) ---
    const directionAttr = el.getAttribute('data-aos-direction');
    const validDirections = ['start', 'end', 'random'];
    const from = validDirections.includes(directionAttr) ? directionAttr : 'start';

    // --- Opacity (0–1, default 0) ---
    const opacityAttr = parseFloat(el.getAttribute('data-aos-opacity'));
    const opacity = isNaN(opacityAttr) ? 0 : Math.min(Math.max(opacityAttr, 0), 1);

    // --- Stagger each (default 0.15s) ---
    const eachAttr = parseFloat(el.getAttribute('data-aos-each'));
    const each = isNaN(eachAttr) ? 0.15 : Math.max(eachAttr, 0);

    // --- Offset fleksibel (3em | 100px | 50% | 10vh | dll) ---
    const offsetAttr = el.getAttribute('data-aos-offset');
    const offset = offsetAttr && offsetAttr.trim() !== '' ? offsetAttr.trim() : '3em';

    gsap.from(children, {
      y: offset,
      duration: 1,
      ease: 'smooth',
      opacity,
      stagger: {
        each,
        from,
      },
    });
  });
}

/**
 * Animasi teks per baris menggunakan GSAP SplitText.
 * Secara otomatis mencari elemen dengan atribut [data-split-lines].
 *
 * @param {Object} options
 * @param {HTMLElement|Document} [options.scope=document] - Scope pencarian elemen (default: seluruh dokumen)
 */
export function splitLines({ scope = document } = {}) {
  const splitElements = scope.querySelectorAll('[data-split-lines]');

  splitElements.forEach((container) => {
    let elements = [];
    const splitRefs = [];
    const lines = [];

    const delay = parseFloat(container.getAttribute('data-delay')) || 0;
    const scrollAttr = container.getAttribute('data-scroll');
    const animateOnScroll = scrollAttr !== 'false'; // default: true

    // Jika elemen punya data-copy-wrapper → targetkan anak-anaknya
    if (container.hasAttribute('data-copy-wrapper')) {
      elements = Array.from(container.children);
    } else {
      elements = [container];
    }

    elements.forEach((element) => {
      const split = SplitText.create(element, {
        type: 'lines',
        mask: 'lines',
        linesClass: 'line++',
        lineThreshold: 0.1,
      });

      splitRefs.push(split);

      // Tangani text-indent agar tidak hilang
      const computedStyle = window.getComputedStyle(element);
      const { textIndent } = computedStyle;
      if (textIndent && textIndent !== '0px') {
        if (split.lines.length > 0) {
          split.lines[0].style.paddingLeft = textIndent;
        }
        element.style.textIndent = '0';
      }

      lines.push(...split.lines);
    });

    // Set posisi awal: turun 100%
    gsap.set(lines, { y: '100%' });

    const animationProps = {
      y: '0%',
      duration: 1,
      stagger: 0.1,
      ease: 'power4.out',
      delay,
    };

    if (animateOnScroll) {
      gsap.to(lines, {
        ...animationProps,
        scrollTrigger: {
          trigger: container,
          start: 'top bottom',
          once: true,
        },
      });
    } else {
      gsap.to(lines, animationProps);
    }

    // Optional: return fungsi cleanup
    return () => splitRefs.forEach((s) => s?.revert());
  });
}
